<template>
 <div class="patientEvaluate">
    <div class="page-header">
      <mt-header>
        <router-link to='/chooseBeCared?clickType=index&serviceType=patient' slot="left">
          <mt-button icon="back">陪护评估</mt-button>
        </router-link>
      </mt-header>
    </div>
    <div class="patientEvaluateBody">
      <div class="selfCatering">
        <p class="navtitle">自理能力等级<span>(以下每子类别需选择一项)</span></p>
        <div class="radioItem" v-for="(t,index) in tpl.selfCareAbilityTpl">
          <p >{{index+1}}.{{t.title}}</p>
          <p class="r" v-for="(cg, i) in t.catalog" @click="handleSelfCareClick(t.title,i)"><input data-t="selfCareAbility" type="radio" :data-score="cg.score" :name="t.title" :ref="t.title":value="cg.desc">{{cg.score}}分{{cg.desc}}</p>
        </div>
      </div> 
      <div class="patientRank">
        <p class="navtitle">病情等级<span>(以下所有选项中选择一项)</span></p>
        <div class="radioItem" v-for="(t,index) in tpl.illnessLevelTpl">
          <p style="font-size:0.4rem;">{{index+1}}.{{t.title}}</p>
          <p class="r" v-for="(cg, i) in t.catalog" @click="handleSelfCareClick(t.title,i,'illnessLevel')"><input data-t="illnessLevel" type="radio" :data-score="cg.score" :name="t.title" :ref="t.title":value="cg.desc">{{cg.score}}分{{cg.desc}}</p>
        </div>
      </div>
      <div style="margin-bottom:1rem;"></div>
    </div>
    <div class="footer">
      <mt-button size="large" @click.native="handleNextClick">下一步</mt-button>
    </div>
  </div>
 </div>
</template>

<script>
import { Toast } from 'mint-ui';
import { mapGetters, mapActions} from 'vuex'
export default {
  name: 'patientEvaluate',
  data() {
    return {
      tpl: {},
      selfCareAbilityNew:[],
      illnessLevel: []
    }
  },
  computed: {
    ...mapGetters({
      glodalVal: 'getGlodalVal'
    })
  },
  methods: {
    ...mapActions({
      changeEvaluateScore: 'changeEvaluateScore',
      changeSelfCareAbility: 'changeSelfCareAbility',
      changeIllnessLevel: 'changeIllnessLevel',
      getIllnessCareAssessTpls: 'getIllnessCareAssessTpls'
    }),
    handleSelfCareClick:function(title,index,op) {
      if(op) {
        this.tpl.illnessLevelTpl.forEach(i => {
          this.$refs[i.title].forEach(j => {
            j.checked = false;
          })
        })
        this.$refs[title][index].checked = true;
      } else {
        this.$refs[title][index].checked = true;
      }
    },
    handleNextClick: function() {
      const ctx = 
      Object.keys(this.$refs).forEach(i => {
        this.$refs[i].forEach(t => {
          if(t.checked == true) {
            const score = t.getAttribute('data-score');
            const title = t.getAttribute('name');
            const desc = t.getAttribute('value');
            const type = t.getAttribute('data-t')
            const obj = {score,title,desc};
            if(type == 'selfCareAbility') {
              let exist = false;
              let id;
              this.selfCareAbilityNew.forEach((k,index) => {
                if(k.title == obj.title) {
                  exist = true;
                  id = index;
                }
              })
              if(exist) {
                this.selfCareAbilityNew[id] = obj;
              } else {
                this.selfCareAbilityNew.push(obj);
              }
            } else {
              this.illnessLevel[0] = obj;
            }
          }
        })
      })
      if(this.selfCareAbilityNew.length != this.tpl.selfCareAbilityTpl.length || this.illnessLevel.length != 1) {
        Toast({
          message: '自理能力的每一项都需要做出评分&病情等级请选择一项',
          iconClass: 'icon icon-error',
          position: 'bottom',
          duration: 1000
        });
      } else {
        this.changeSelfCareAbility(this.selfCareAbilityNew);
        this.changeIllnessLevel(this.illnessLevel);
        let score = 0;
        this.selfCareAbilityNew.forEach(function(i) {
          score += parseInt(i.score);
        })
        this.illnessLevel.forEach(function(i) {
          score += parseInt(i.score);
        }) 
        this.changeEvaluateScore(score);
        this.$router.push({ path: '/jrServicesList' })
      }
    }
  },
  created() {
    this.getIllnessCareAssessTpls(-1).then(response => {
      if(response.ok) {
        this.tpl = response.data;
      }
    })
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.patientEvaluateBody {
  height: 90%;
  overflow: scroll;
  padding: 0 3%;
}
.patientEvaluate .navtitle{
  margin-top: 10px;
  font-size: 0.4rem;
  color: #565256;
  height: 1rem;
  line-height: 0.6rem;
  border-bottom: 1px solid #d06091;
}
.patientEvaluate .navtitle span {
  font-size: 0.4rem;
  color: #d06091;
}
.top {
  margin-top: 30px;
}
.mint-radio-input:checked + .mint-radio-core  {
  background-color: #d06091 !important;
  border-color: #efeeef;
}
.footer {
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    z-index: 1;
}
.footer .mint-button {
  height: 1rem;
}
.radioItem {
  font-size: 0.4rem;
  color: #565256;
  margin: 0.2rem 0;
}
.radioItem input[type="radio"] {
  width: 0.3rem;
  height: 0.3rem;
  display: inline-block;
  margin-right: 0.2rem;
}
.r {
  margin: 0.2rem;
}
</style>



